@import url('https://fonts.googleapis.com/css?family=Big+Shoulders+Text|Roboto&display=swap');

.main,
.about,
.portfolio {
  background-color: #1a1a2e;
  color: #fcdab7;
  width: 100%;
  text-align: center;
  overflow: auto;
  display: block;
}

.portfolio {
  display: flex;
  align-content: center;
  justify-content: center;
}

.container {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.logo {
  margin: 1rem;
}

.logo g {
  fill: #e94560;
}

header {
  text-align: left;
  display: flex;
}

h1,
h2 {
  font-family: 'Big Shoulders Text', cursive;
  padding: 1rem;
}

h1 {
  font-size: 2.5rem;
}

h2 {
  font-size: 2rem;
}

p,
ul {
  font-family: 'Roboto', sans-serif;
  font-size: 1rem;
  list-style-type: none;
  padding: 0.5rem;
  margin-bottom: 2rem;
}

.about a {
  color: #e94560;
  text-decoration: none;
}

.about a:hover {
  color: #f6b5bf;
  transition: all 300ms ease-in-out;
}

@media only screen and (max-width: 375px) {
  p,
  ul {
    text-align: left;
  }
}
