@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css?family=Big+Shoulders+Text|Roboto&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  font-size: 1rem;
}

.App {
  display: flex;
  min-height: 100vh;
  height: 100%;
  background-color: #16213e;
}

.navbar {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #16213e;
  height: 100%;
  width: 4rem;
  font-size: 4rem;
  flex-grow: 1;
}

.nav-item {
  padding: 1rem 0 1rem 0;
  transition: 500ms;
  width: 100%;
  text-align: center;
  color: #e94560;
}

.nav-item a {
  text-decoration: none;
}

.nav-item:hover {
  background-color: #0f3460;
  transition: 300ms;
}

.main,
.about,
.portfolio {
  background-color: #1a1a2e;
  color: #fcdab7;
  width: 100%;
  text-align: center;
  overflow: auto;
  display: block;
}

.portfolio {
  display: flex;
  align-content: center;
  justify-content: center;
}

.container {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.logo {
  margin: 1rem;
}

.logo g {
  fill: #e94560;
}

header {
  text-align: left;
  display: flex;
}

h1,
h2 {
  font-family: 'Big Shoulders Text', cursive;
  padding: 1rem;
}

h1 {
  font-size: 2.5rem;
}

h2 {
  font-size: 2rem;
}

p,
ul {
  font-family: 'Roboto', sans-serif;
  font-size: 1rem;
  list-style-type: none;
  padding: 0.5rem;
  margin-bottom: 2rem;
}

.about a {
  color: #e94560;
  text-decoration: none;
}

.about a:hover {
  color: #f6b5bf;
  transition: all 300ms ease-in-out;
}

@media only screen and (max-width: 375px) {
  p,
  ul {
    text-align: left;
  }
}

.card {
  border-radius: 10px;
  height: 15rem;
  width: 10rem;
  margin: 1rem;
  align-items: center;
  display: flex;
  flex-direction: column;
}

.content {
  margin: 0;
  background-color: #0f3460;
}

.title {
  margin-top: 20%;
  background-color: #0f3460;
  width: 100%;
  box-shadow: 0 5px 10px -5px #000;
}

.description {
  background-color: #0f3460;
  border-radius: 10px;
  padding: 1rem 0.5rem;
  text-align: left;
  flex-grow: 1;
}

.gitLink {
  padding-bottom: 1rem;
}

.card a {
  text-decoration: none;
  color: inherit;
  transition: 300ms;
}

.card a:hover {
  color: #e94560;
  transition: 300ms;
}

